
.container {
  max-width: 100%;
  height: 570px;
  right: 10%;
  width: 95%;
  left: 20%;
  padding-top: 6%;
  padding-left: 1%;
  margin-left: 3%;
  top: 2150px;
  box-shadow: 2px 13px 76px 2px rgba(0,0,0,0.75);
  left: 0;
}
.skillno{
  color: var(--buttontext);
}
.skills {
  margin: 0 0 3.6% 0;
  padding: 0;
  width: calc(100% - 5%);
  position: absolute;
  top: 70px;
}
  li {
    display: block;
    position: relative;
    background-color: var(--primaryskills);
    color: var(--buttontext);
    margin-left: 10px;
    margin: 1% 0;

    padding-left: 1%;
    transition: width 300ms ease-in-out;
  }
  .a{
    position: absolute;
    top: 5%;
  }
    .collapsed  {
      width: 0 !important;
    }
    p {
      padding: 10px;
      margin: 0;
      font-weight: bold;
    }
    span {
      position: absolute;
      right: 10px;
      display: inline-block;
      width: 30px;
      top: 11px;
      text-align: right;
      font-weight: normal;
      color: black;
      font-size: 11px;
    }
  


body {
  font-size: 13px;

}

hr {
  height: 1px;
  border: 0;
  background-color: #ccc;
  margin: 20px 0;
  padding: 0;
}

small {
  font-size: 11px;
  color: #666;
  
}
.tran {
  transition-duration: 3s;
}
@media (width: 768px) {
  .container{
  
      
    margin-top: 500px
  }

}
@media (max-width: 800px) {
  .container{
      position: absolute;
      
      top: 10000px;
  }

}
@media (max-width: 600px) {
  .container{
      top: 6990px;
      margin-top: 100px;

  }

}
@media (max-width: 1100px) {
  .container{
    top: 1700px;
}

}

@media (max-width: 450px) {
  .container{
      top: 2300px;
      height: 530px;
  }
}
  @media (max-width: 415px) {
    .container{
        top: 2300px;
        height: 530px;
      
    
      }
     .a{
      margin-top: 20px;
     }  
  }
  .skills {
    margin: 20px 50px 3.6% 10px;
    
    padding: 0;
    width: calc(100% - 5%);
    position: absolute;
    top: 70px;
  }
 
 

